/* Book style */
.book-wrapper {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 1em;
  }
  .book {
    padding: 2em;
    box-shadow: 2px 2px 10px #808080b0;
    border-radius: 0.5em;
    text-align: center;
  }
  .book-title {
    font-size: 1.4em;
    color: #507550;
    font-weight: bold;
  }
  .book-author {
    font-size: 1.1em;
  }
  .book-genre {
    font-size: 0.9em;
  }
  .book-span-descr {
    text-transform: uppercase;
    font-size: 0.8rem;
    font-weight: bold;
    margin-right: 1em;
    color: #222;
  }

  /* Media query */
@media only screen and (max-width: 500px) {

   .book-wrapper {
    grid-template-columns: 1fr;
  }
}
@media only screen and (min-width:501px) and (max-width: 990px) {

  .book-wrapper {
    grid-template-columns: 1fr;
  }
}